	@media screen and (max-width: 767px) {
		.scroll-up {
			position: fixed;
			bottom: 17px;
			right: 17px;
			width: 54px;
			height: 54px;
			padding: 15px;
			background-color: var(--color-violet);
			border-radius: 50%;
			box-shadow: 0px 0px 0 5px rgba(79, 46, 232, 0.3);

			&:hover,
			&:focus {
				transform: scale(1.1);
			}
		}
	}

