body {
    background-color: var(--background-color);
    color: var(--font-color);
    font-family: 'DM Sans Regular', sans-serif;
}

.container {
	width: 375px;
  padding: 0 20px;
  margin: 0 auto;

	@media screen and (min-width: 768px) {
    width: 768px;
		padding: 0 32px;
	}

	@media screen and (min-width: 1440px) {
    width: 1440px;
		padding: 0 24px;
	}
}

// .is-hidden {
// 	display: block;
// }

.is-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
