}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 24px;
  }
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.check-text {
  align-items: center;
  gap: 5px;
  margin-bottom: 2px;
  display: flex;
  position: relative;
}

.check-text:hover {
  cursor: pointer;
}

.check-text:before {
  content: "";
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border: .1px solid #c3baeb;
  border-radius: 50px;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.check-text:after {
  content: "";
  width: 18px;
  height: 18px;
  background-color: #fff;
  border: .1px solid #c3baeb;
  border-radius: 50%;
  transition-duration: .25s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.check-theme:checked + .check-text:after {
  transform: translate(20px);
}

.dark-mode {
  color: #fff;
  background-color: #202024;
}

.dark-mode .check-text:before {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #d5cfcf;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 767px) {
  .scroll-up {
    width: 54px;
    height: 54px;
    background-color: var(--color-violet);
    border-radius: 50%;
    padding: 15px;
    position: fixed;
    bottom: 17px;
    right: 17px;
    box-shadow: 0 0 0 5px rgba(79, 46, 232, .3);
  }

  .scroll-up:hover, .scroll-up:focus {
    transform: scale(1.1);
  }
}

